.contactList {
  list-style: none;
  padding: 8px;
}

.contact {
  display: flex;
  flex-direction: row;
  padding: 12px;
  align-content: center;
  width: 450px;
  justify-content: space-between;
  padding: 10px 50px;
  border-radius: 2px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
  margin-bottom: 5px;
  /* margin: 0 auto; */
}

.button {
  /* height: 30%; */
  align-self: center;
  color: #ffffff;
  background-color: red;
  border: none;
  padding: 4px 8px;
  border-radius: 5px;
}

.contact {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 450px;
  height: 60px;
  padding: 12px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
  border-radius: 2px;
}

.contact:not(:last-child) {
  margin-bottom: 10px;
}
